@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.hero {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  margin-top: 6rem;
  background-color: rgb(255, 120, 120);
}
.hero > div {
  flex: 1;
}
.hero .img img {
  width: 100%;
}
.hero .content > * {
  margin: 1.9rem 0;
  display: block;
}
.hero .content h1 {
  font-weight: 700;
  font-size: 2.5rem;
}
.hero .content p {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  max-width: 55%;
}
.hero .content button {
  font-weight: 700;
  font-size: 1rem;
  padding: 16px 40px;
  border-radius: 32px;
  background: #e4c1ae;
  color: #fff;
  border: none;
}

.hidden {
  display: none;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 60%;
  padding: 2rem 5rem;
}
.modal h1 {
  font-weight: 700;
  font-size: 2.5rem;
}
.modal p {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
}
.modal .close-modal {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background: none;
  font-size: 1.5rem;
  font-family: "Roboto", sans-serif;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.5);
}